.section {
  margin-bottom: 100px;
}

.section-title {
  position: relative;
  font-size: 20px;
  text-transform: uppercase;
  margin-bottom: 45px;
}

.section-title::before {
  position: absolute;
  content: '';
  width: 100%;
  height: 1px;
  background-color: #e0e2e6;
  left: 0;
  top: 50%;
}

.section-title span {
  position: relative;
  padding-right: 30px;
  background-color: #fff;
}

#breadcrumbs {
  margin: 0 0 15px;
  font-size: 13px;
}

ul.page-numbers {
  padding: 40px 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

ul.page-numbers li {
  margin: 0 3px;
}

ul.page-numbers a {
  color: #94adba;
  font-size: 14px;
  font-weight: 500;
  text-decoration: none;
}

ul.page-numbers .prev,
ul.page-numbers .next {
  position: relative;
}

ul.page-numbers .prev::before, ul.page-numbers .prev::after,
ul.page-numbers .next::before,
ul.page-numbers .next::after {
  position: absolute;
  content: '';
  background-color: #94adba;
  width: 10px;
  height: 1px;
}

ul.page-numbers .prev {
  padding: 0 45px 0 17px;
}

ul.page-numbers .prev::before {
  transform: rotate(-45deg);
  top: 6px;
  left: -1px;
}

ul.page-numbers .prev::after {
  transform: rotate(45deg);
  bottom: 5px;
  left: -1px;
}

ul.page-numbers .next {
  padding: 0 17px 0 45px;
}

ul.page-numbers .next::before {
  transform: rotate(45deg);
  top: 6px;
  right: -1px;
}

ul.page-numbers .next::after {
  transform: rotate(-45deg);
  bottom: 5px;
  right: -1px;
}

ul.page-numbers .current {
  color: #143848;
}

