.about .section-text h2 {
  margin: 0 0 14px;
  font-size: 28px;
  font-weight: 700;
  text-transform: none;
  line-height: 30px;
}

.about .section-text h3 {
  margin: 0 0 10px;
  text-transform: none;
}

.about .section-text p {
  margin: 0 0 10px;
  font-size: 14px;
  line-height: 21px;
}

.reviews .section-wrap {
  background-color: #ddd4d9;
  background-image: linear-gradient(-58deg, rgba(248, 202, 214, 0.61) 0%, rgba(186, 105, 167, 0.61) 100%);
}

.reviews .reviews-wrap {
  padding-top: 30px;
  display: flex;
}

.reviews .left {
  padding-top: 80px;
  width: 58.3333%;
}

.reviews .right {
  width: 41.6667%;
  text-align: center;
}

.reviews .reviews-icon {
  text-align: center;
  margin-bottom: 25px;
}

.reviews .reviews-item {
  padding: 0 95px;
  text-align: center;
  margin-bottom: 40px;
}

.reviews .reviews-text {
  font-size: 14px;
  font-style: italic;
  line-height: 24px;
  margin-bottom: 30px;
}

.reviews .reviews-pagination {
  text-align: center;
}

.reviews .swiper-pagination-bullet {
  width: 10px;
  height: 10px;
  background: transparent;
  border: 1px solid #404040;
  opacity: 1;
}

.reviews .swiper-pagination-bullet-active {
  background: #404040;
}

.reviews .swiper-container-horizontal > .swiper-pagination-bullets .swiper-pagination-bullet {
  margin: 0 5px;
}

.feedback .section-wrap {
  display: flex;
}

.feedback .feedback-descr,
.feedback .feedback-img {
  width: calc(25% - 30px);
  margin-right: 30px;
  background-size: cover;
  min-height: 315px;
}

.feedback .feedback-descr {
  padding: 60px;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: 20px;
  line-height: 30px;
  text-align: center;
  position: relative;
  background-image: url("../img/f1.jpg");
}

.feedback .feedback-descr::before {
  position: absolute;
  content: '';
  top: 30px;
  left: 30px;
  right: 30px;
  bottom: 30px;
  border: 10px solid #fff;
}

.feedback .feedback-descr * {
  position: relative;
}

.feedback .feedback-img {
  background-image: url("../img/f2.jpg");
}

.feedback .feedback-text {
  width: 50%;
  display: flex;
  flex-direction: column;
  justify-content: center;
  align-items: center;
  text-align: center;
}

.feedback .feedback-title {
  font-size: 28px;
  font-weight: 700;
  margin-bottom: 25px;
}

.feedback .feedback-email {
  font-size: 14px;
  font-weight: 300;
  padding-bottom: 15px;
  margin-bottom: 30px;
  position: relative;
}

.feedback .feedback-email::before {
  position: absolute;
  content: '';
  width: 325px;
  height: 1px;
  bottom: 0;
  left: 50%;
  transform: translateX(-50%);
  background-color: #b9bbbe;
}

.feedback .feedback-link {
  margin-bottom: 35px;
}

.feedback .feedback-notice {
  font-size: 12px;
  font-weight: 300;
  line-height: 18px;
}

.feedback .feedback-notice a {
  color: #404040;
}

.instagram {
  padding: 10px 0;
  position: relative;
  background-color: #f4f5f7;
}

.instagram .instagram-title {
  padding: 18px 25px;
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate3d(-50%, -50%, 0);
  font-size: 14px;
  line-height: 24px;
  background-color: rgba(255, 255, 255, 0.9);
  z-index: 2;
}

.instagram .instagram-gallery {
  display: flex;
}

.instagram .instagram-gallery a {
  display: block;
  width: 100%;
  height: auto;
}

.instagram .instagram-gallery a img {
  width: auto;
  height: auto;
}

.instagram .instagram-gallery a:not(:last-child) {
  margin-right: 10px;
}

.page-404 {
  padding: 100px 20px;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.page-404 .page-404-title {
  margin-bottom: 30px;
  color: transparent;
  font-family: "Lora", serif;
  font-size: 250px;
  font-weight: 700;
  line-height: 186px;
  text-transform: uppercase;
  -webkit-background-clip: text;
  background-position: 34% 47%;
  background-image: url("../img/bg-404.jpg");
}

@media only screen and (max-width: 575px) {
  .page-404 .page-404-title {
    font-size: 150px;
    line-height: 110px;
  }
}

.page-404 .page-404-descr {
  color: #1d1d1d;
  font-family: "Lora", serif;
  font-size: 24px;
  font-weight: 400;
  line-height: 27px;
  text-transform: uppercase;
  margin-bottom: 35px;
}

.page-404 .page-404-descr:first-of-type {
  margin-bottom: 25px;
}

.page-404 .page-404-btn {
  margin-bottom: 40px;
}

.page-404 .page-404-link a {
  color: #1d1d1d;
  font-size: 13px;
  line-height: 19px;
  text-decoration: underline;
}

.page-404 .page-404-link a:hover {
  text-decoration: none;
}
